.floating-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  background-color: #095e1c;
  color: white;
  border: none;
  outline: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
  z-index: 100;
  opacity: 1;
  visibility: visible;
}

.floating-button:hover {
  background-color: #0056b3;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.floating-button.hidden {
  opacity: 0;
  visibility: hidden;
}
